<template>
    <div class="listviewTop">
        <img class="bgpic" :src="details.coverImgUrl" alt="">
        <div class="listviewTopNav">
            <div class="back" @click="$router.go(-1)">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-zuojiantou"></use>
                </svg>
                <div class="title">歌单</div>
            </div>
            <div class="search">
                <svg class="icon searchpic" aria-hidden="true">
                    <use xlink:href="#icon-sousuo"></use>
                </svg>
                <svg class="icon listdian" aria-hidden="true">
                    <use xlink:href="#icon-diandiandianshu"></use>
                </svg>
            </div>
        </div>
        <div class="content">
            <div class="contentleft">
                <img :src="details.coverImgUrl" alt="">
                <span>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-w24gl-play"></use>
                    </svg>
                    {{changeVaule(details.playCount)}}
                </span>
            </div>
            <div class="contentright">
                <h4>{{details.name}}</h4>
                <div class="author">
                    <img class="header" :src="details.creator.avatarUrl" alt="">
                    <span>
                        {{details.creator.nickname}}
                        <span>
                            <svg class="icon" aria-hidden="true">
                                <use xlink:href="#icon-arrow-right"></use>
                            </svg>
                        </span>
                    </span>
                </div>
                <div class="description">
                    {{details.description}}
                </div>
            </div>
        </div>
        <div class="iconlist">
            <div class="iconitem">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-pinglun"></use>
                </svg>
                <span>{{details.commentCount}}</span>
            </div>
            <div class="iconitem">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-fenxiang"></use>
                </svg>
                <span>{{details.shareCount}}</span>
            </div>
                
            <div class="iconitem">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-xiazai"></use>
                </svg>
                <span>下载</span>
            </div>
            
            <div class="iconitem">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-duoxuan-"></use>
                </svg>
                <span>多选</span>
            </div>
            
        </div>
    </div>
</template>

<script>
export default {
    props:['details'],
    methods: {
        changeVaule(num){
            let res = 0;
            if(num>=100000000){
                res = num / 100000000
                res = res.toFixed(2) + '亿'
                return res
            }else if(num>=10000){
                res = num / 10000
                res = res.toFixed(2) + '万'
                return res
            }
        }
    }
}
</script>

<style lang="less" scoped>
.listviewTop {
    width: 7.5rem;
    
    .bgpic{
        position: fixed;
        left: 0;
        top: 0;
        width: 7.5rem;
        height: auto;
        z-index: -1;
        filter: blur(45px);
    }
    .listviewTopNav {
        display: flex;
        justify-content: space-between;
        height: 1.0rem;
        padding: 0.2rem 0.3rem;
        font-size: 0.33rem;
    }
    .back,
    .right {
        display: flex;
        color: #fff;
    }
    .icon {
        width: 0.5rem;
        height: 0.5rem;
        margin-right: 0.2rem;
        fill: #fff;
    }
    .searchpic {
        width: 0.38rem;
        height: 0.38rem;
        margin-right:0.4rem;
        line-height: 0.38rem;
    }
    .listdian {
        margin-right: 0;
    }
    .content {
        display: flex;
        padding: 0.25rem;
        justify-content: space-between;
        .contentleft{
            position: relative;
            img{
                width: 2.7rem;
                height: 2.7rem;
                border-radius: 0.1rem;
            }
            span{
                position: absolute;
                right: 0.1rem;
                top: 0.05rem;
                font-size: 0.23rem;
                color: #fff;
                .icon{
                    width: 0.22rem;
                    height: 0.22rem;
                    margin-right: 0;
                }
            }
        }
        .contentright {
            width: 4.0rem;
            h4{
                color: #fff;
                padding: 0.1rem 0;
                font-weight: 600;
                font-size: 0.33rem;
            }
            .author{
                display: flex;
                align-items: center;
                font-size: 0.24rem;
                color: rgb(223, 220, 220);
                img.header{
                    width: 0.6rem;
                    height: 0.6rem;
                    border-radius: 0.3rem;
                    margin-right: 0.15rem;
                    margin-top: 0.1rem;
                }
                span{
                    position: relative;
                    .icon{
                        position: absolute;
                        top: 0.043rem;
                        right: -0.45rem;
                        width: 0.23rem;
                        height: 0.23rem;
                    }
                }
                
            }
            .description{
                font-size: 0.24rem;
                margin: 0.1rem 0;
                color: rgb(223, 220, 220);
                font-size: 0.22rem;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 3;
                -webkit-box-orient: vertical;
            }
        }
    }
    .iconlist {
        width: 7.5rem;
        height: 1.0rem;
        padding: 0 0.8rem;
        display: flex;
        justify-content: space-between;
        .iconitem{
            display: flex;
            flex-direction: column;
            align-items: center;
            color: #fff;
            font-size: 0.25rem;
            .icon{
                width: 0.45rem;
                height: 0.45rem;
            }
            span{
                width: 0.6rem;
                height: 0.4rem;
            }

        }
    }
}
</style>