<template>
<div class="box">
    <div class="back" @click="$router.go(-1)">
        <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-zuojiantou"></use>
        </svg>
    </div>
    <div class="login-contain">
    <div class="login-header">
        <img src="@/assets/imgs/logo.svg" alt="">
        <p>欢迎登录</p>
    </div>
    <div class="form-group">
        <div class="form-item">
            <label for="username">
                <img src="@/assets/imgs/user.png">
            </label>
            <input id="username" v-model="phone" type="text" placeholder="请输入手机号" autocomplete="off">
            <button class="send" @click="send">发送</button>
        </div>
        <div class="form-item">
            <label for="password">
                <img src="@/assets/imgs/password.png" alt="">
            </label>
            <input id="password" v-model="password" type="num" placeholder="请输入验证码" autocomplete="off">
        </div>
    </div>
    <div class="button-group">
        <button class="login-btn" id="doLogin" @click="forcertain">登录</button>
    </div>

    <div class="order-login">
        <p class="order-login-line">其他登录方式</p>
        <div class="order-login-box">
            <div>
                <a href="#">
                    <img src="@/assets/imgs/wx.svg" alt="" style="width: 45px;height: 45px;">
                    <p>微信登录</p>
                </a>
            </div>
        </div>
    </div>
    </div>
</div>
    

    
</template>

<script>
export default {
    data () {
        return {
            phone:'',
            password:''
        }
    },
    methods: {
        forcertain: async function(){
            if(this.phone.trim() != '' && this.password.trim() != ''){
                let res = await this.$store.dispatch('forcertain', {phone:this.phone, password:this.password})
                console.log(res);
                if(res){
                    this.$router.push('/me')
                }else{
                    alert('Please input correct phone captcha!!!!')
                    this.password = ''
                }
            }else{
                alert("Please input!!!!!!")
            }
            
        },
        send(){
            let myreg=/^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            if(myreg.test(this.phone)){
                this.$store.dispatch('send', {phone:this.phone})
            }else{
                alert('Please input correct phone number!!!!')
                this.phone = ''
            }
            
        }
    }
}
</script>

