<template>
    <div class="playlist">
        <div class="playlistTop">
            <div class="left">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-24gl-playCircle"></use>
                </svg>
                <div class="title">播放全部</div>
                <div class="num">(共{{details.tracks.length}}首)</div>
            </div>
            <div class="right">
                <div class="btn">
                    + 收藏 ({{changeValue(details.subscribedCount)}})
                </div>
            </div>
        </div>
        <div class="list">
            <div class="item" v-for="(item, i) in details.tracks" :key="i"  @click="setPlayIndex(i)">
                <div class="left">
                    <div class="index">{{i+1}}</div>
                    <div class="content">
                        <div class="title">{{item.name}}</div>
                        <div class="author">
                            <span class="tag" v-for="(tag, index) in details.tags" :key="index">{{tag}}</span>
                            <span class="name">{{item.ar[0].name}} - {{item.al.name}}</span>
                        </div>
                    </div>
                </div>
                <div class="right">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-shipin"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-diandiandianshu"></use>
                    </svg>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {mapMutations} from 'vuex'

export default {
    props:['details'],
    methods: {
        changeValue(num){
            let res = 0;
            if(num>=100000000){
                res = num / 100000000
                res = res.toFixed(2) + '亿'
                return res
            }else if(num>=10000){
                res = num / 10000
                res = res.toFixed(2) + '万'
                return res
            }else{
                return num
            }
        },
        ...mapMutations(['setPlayIndex'])
    },
    
}
</script>

<style lang="less" scoped>
.playlist{
    width: 7.5rem;
    background-color: #fff;
    margin-top: 0.1rem;
    padding: 0.02rem 0.22rem;
    border-top-left-radius: 0.25rem;
    border-top-right-radius: 0.25rem;
    .playlistTop{
        display: flex;
        height: 1rem;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 0.15rem;
        
        .left{
            display: flex;
            justify-content: space-between;
            align-items: center;
            .icon{
                width: 0.5rem;
                height: 0.5rem;
                fill: #333;
                margin-right: 0.2rem;
            }
            .title{
                font-size: 0.35rem;
                margin: 0 0.1rem;
            }
            .num {
                color: rgb(175, 175, 175);
                font-size: 0.24rem;
            }
        }
        .right{
            width: 2.2rem;
            height: 0.8rem;
            background-color: rgb(252, 59, 59);
            color: #fff;
            font-size: 0.25rem;
            text-align: center;
            line-height: 0.75rem;
            border-radius: 0.4rem;
        }
    }
    .list{
        .item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background-color: #fff;
            height: 1rem;
            margin-bottom: 0.2rem;
            .left {
                display: flex;
                align-items: center;
                width: 5.1rem;
                .index{
                    width: 0.15rem;
                    margin-left: 0.13rem;
                    color: rgb(175, 175, 175);
                }
                .content{
                    margin-left: 0.4rem;
                }
                .title {
                    font-size: 0.32rem;
                    overflow: hidden;
                    display: -webkit-box;
                    text-overflow: ellipsis;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                }
                .author{
                    overflow: hidden;
                    display: -webkit-box;
                    text-overflow: ellipsis;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    .tag {
                        margin: 0rem 0.03rem;
                        padding: 0 0.01rem;
                        border: 0.02rem solid rgb(252, 59, 59);
                        border-radius: 0.05rem;
                        font-size: 0.18rem;
                        color: rgb(252, 59, 59);
                    }
                    .name{
                        font-size: 0.25rem;
                        color: #666;
                        margin-left: 0.1rem;
                        overflow: hidden;
                        
                    }
                }
                
            }
            .right {
                .icon {
                    width: 0.5rem;
                    height: 0.5rem;
                    margin-left: 0.20rem;
                    fill: rgb(175, 175, 175);
                }
            }
        }
    }
}

</style>