<template>
    <div class="searchTop">
        <div class="searchTopNav">
            <div class="back" @click="$router.go(-1)">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-zuojiantou-copy"></use>
                </svg>
            </div>
            <div class="search">
                <input type="text" v-model="sreachKeyword" :placeholder="placeholder" @keydown.enter="saveKeyword">
            </div>
        </div>
        <div class="history">
            <div class="historyLfet">
                历史
            </div>
            <div class="historyRight">
                <div @click="historySearch(item)" class="keywordItem" v-for="(item, i) in keywordList" :key='i' >{{item}}</div>
                <svg class="icon" aria-hidden="true" @click="deleteHis">
                    <use xlink:href="#icon-shanchu"></use>
                </svg>
            </div>
        </div>
            <div class="playlist">
                <div class="playlistTop">
                    <div class="left" v-show="searchSongs.length">
                        <svg class="icon" aria-hidden="true">
                            <use xlink:href="#icon-24gl-playCircle"></use>
                        </svg>
                        <div class="title">播放全部</div>
                        <div class="num">(共{{searchSongs.length}}首)</div>
                    </div>
                    <div class="right">

                    </div>
                </div>
            <div class="list">
                <div class="item" v-for="(item, i) in searchSongs" :key="i"  @click="setPlay(item,i)">
                    <div class="left">
                        <div class="index">{{i+1}}</div>
                        <div class="content">
                            <div class="title">{{item.name}}</div>
                            <div class="author">
                                <span class="tag" v-show="item.pop>66">pop</span>
                                <span class="name">{{item.ar[0].name}} - {{item.al.name}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="right">
                        <svg class="icon" aria-hidden="true">
                            <use xlink:href="#icon-shipin"></use>
                        </svg>
                        <svg class="icon" aria-hidden="true">
                            <use xlink:href="#icon-diandiandianshu"></use>
                        </svg>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {searchMusic} from '@/api/index.js'

export default {
   data () {
       return {
           placeholder:"陈奕迅",
           keywordList:[],
           sreachKeyword:'',
           searchSongs:[],
       }
   },
   beforeMount: async function () {
    //    localStorage.keywordList = []
       this.keywordList = localStorage.keywordList ? JSON.parse(localStorage.keywordList):[]
       let res =  await searchMusic('周杰伦')
        this.searchSongs = res.data.result.songs
        this.$store.state.playlist = res.data.result.songs
   },
   methods: {
       saveKeyword:async function(){
           if(this.sreachKeyword.trim() != ''){
               this.keywordList.push(this.sreachKeyword)
               this.keywordList.reverse()
               this.keywordList = Array.from(new Set(this.keywordList))
           }
           if(this.keywordList.length > 10 ){
               this.keywordList = this.keywordList.slice(0,10)
           }
           localStorage.keywordList = JSON.stringify(this.keywordList)
           
           let res =  await searchMusic(this.sreachKeyword)
           this.searchSongs = res.data.result.songs
            this.$store.state.playlist = res.data.result.songs
            console.log(res);
           this.sreachKeyword = ''
       },
       deleteHis: function(){
           this.keywordList = []
           localStorage.keywordList = [];
       },
       historySearch(keyword){
           this.sreachKeyword = keyword
           this.saveKeyword()
       },
       setPlay(item, i){
        //    item.al = item.album
           this.$store.commit('pushPlaylist', item)
           this.$store.commit('setPlayIndex', this.$store.state.playlist.length-1)
       }

   }
}
</script>

<style lang="less" scoped>
.searchTop {
    width: 7.5rem;
    height: calc(100vh - 0.6rem);
    overflow: scroll;
    padding: 0 0.3rem;
    .searchTopNav{
        display: flex;
        width: 100%;
        background-color: #fff;
        z-index: 100;
        height: 1.3rem;
        align-items: center;
        position: fixed;
        top: 0;
        left: 0;
        padding: 0 0.3rem;
        .icon{
            width: 0.65rem;
            height: 0.65rem;
            
        }
        .search{
            color: rgb(175, 175, 175);
            padding: 0 0.2rem;
            flex: 1;
            input{
                width: 5.8rem;
                height: 0.7rem;
                font-size: 0.33rem;
                padding: 0.05rem 0;
                text-indent: 0.05rem;
                line-height: 0.6rem;
                color: rgb(175, 175, 175);
                border: none;
                border-bottom: 1.5px solid #666;
                outline-style: none;
            }
        }
    }

    .history {
        position: relative;
        margin-top: 1.3rem;
        width: 7.5rem;
        display: flex;
        .historyLfet{
            width: 0.7rem;
            height: 0.4rem;
            font-weight: 600;
            margin: 0.12rem 0rem;
            line-height: 0.4rem;
        }
        .historyRight{
            color: #666;
            display: flex;
            flex-wrap: wrap;
            padding: 0rem 0.9rem 0rem 0rem;
            flex: 1;
            height: 1.3rem;
            overflow: hidden;
            .keywordItem {
                background-color: #eee;
                padding: 0 0.3rem;
                height: 0.5rem;
                font-size: 0.27rem;
                border-radius: 0.4rem;
                margin: 0.1rem;
                line-height: 0.5rem;
            }
            .icon {
                position: absolute;
                right: 0.65rem;
                top: -0.3rem;
                width: 1.2em;
                height: 1.2rem;
            }
        }
    }
    .playlist{
        width: 7.5rem;
        background-color: #fff;
        margin-top: 0.1rem;
        padding: 0rem 0.4rem 0rem 0rem;
        .playlistTop{
            display: flex;
            height: 1rem;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 0.15rem;
            
            .left{
                display: flex;
                justify-content: space-between;
                align-items: center;
                .title{
                    font-size: 0.35rem;
                    margin: 0 0.1rem;
                }
                .num {
                    color: rgb(175, 175, 175);
                    font-size: 0.24rem;
                }
            }
            
        }
        .list{
            .item {
                display: flex;
                justify-content: space-between;
                align-items: center;
                background-color: #fff;
                height: 1rem;
                margin-bottom: 0.1rem;
                .left {
                    display: flex;
                    align-items: center;
                    width: 5.1rem;
                    .index{
                        width: 0.15rem;
                        margin-left: 0.13rem;
                        color: rgb(175, 175, 175);
                    }
                    .content{
                        margin-left: 0.4rem;
                    }
                    .title {
                        font-size: 0.32rem;
                        overflow: hidden;
                        display: -webkit-box;
                        text-overflow: ellipsis;
                        -webkit-line-clamp: 1;
                        -webkit-box-orient: vertical;
                    }
                    .author{
                        overflow: hidden;
                        display: -webkit-box;
                        text-overflow: ellipsis;
                        -webkit-line-clamp: 1;
                        -webkit-box-orient: vertical;
                        .tag {
                            margin: 0rem 0.03rem;
                            padding: 0rem 0.01rem;
                            border: 0.02rem solid rgb(252, 59, 59);
                            border-radius: 0.05rem;
                            font-size: 0.2rem;
                            line-height: 0.1rem;
                            color: rgb(252, 59, 59);
                        }
                        .name{
                            font-size: 0.25rem;
                            color: #666;
                            margin-left: 0.1rem;
                            overflow: hidden;
                            
                        }
                    }
                    
                }
                .right {
                    .icon {
                        width: 0.5rem;
                        height: 0.5rem;
                        margin-left: 0.20rem;
                        fill: rgb(175, 175, 175);
                    }
                }
            }
        }
    }
}

</style>