<template>
    <div class="listvew">
        <listview-top :details="state.details"></listview-top>
        <play-list :details="state.details"></play-list>
    </div>
</template>

<script>
import listviewTop from '@/components/listviewTop.vue'
import {getPlayList} from '@/api/index.js'
import {onMounted, reactive, } from 'vue'
import {useRoute} from 'vue-router'
import ListView from './ListView.vue'
import PlayList from '@/components/PlayList.vue'
import store from '@/store/index.js'


export default {
  components: { ListView, listviewTop, PlayList },
    setup(){
        const route = useRoute()
        let state = reactive({
            list:[],
            details:{
                creator:{},
                tracks:[]
            }
            })
        onMounted(async()=>{
            console.log(route);
            let res = await getPlayList(route.query.id);
            console.log(res)
            state.details = res.data.playlist
            store.commit('setPlaylist', state.details.tracks)
        })
        return {
            state
        }
    }
}
</script>